import { setContext, getContext } from 'svelte';
import { writable, get } from 'svelte/store';

const CONTEXT_KEY = Symbol("graph");

export default class GraphState
{
    static SetContext() 
    {
        return setContext(CONTEXT_KEY, new GraphState());
    }

    static GetContext()
    {
        return getContext(CONTEXT_KEY);
    }

    constructor()
    {
        const graph = this.#LoadFromLocalStorage();

        this.nodes = writable(graph.nodes);
        this.edges = writable(graph.edges);
    }

    #LoadFromLocalStorage() 
    {
        if (localStorage.graph)
            return JSON.parse(localStorage.graph);

        const defaultGraph = {"nodes":[{"id":"1","type":"textInput","position":{"x":-20,"y":0},"measured":{"width":182,"height":104},"data":{"value":"What is AGI?"},"selected":false,"dragging":false},{"id":"2","type":"generate","position":{"x":240,"y":0},"measured":{"width":459,"height":350},"selected":true,"dragging":false,"width":459,"height":350,"data":{"markdown":"","html":""}}],"edges":[{"source":"1","target":"2","selected":false}]};

        return defaultGraph;
    }

    LoadFromFile(graph)
    {
        this.nodes.set(graph.nodes);
        this.edges.set(graph.edges);
        this.Save();
    }

    AddNode(node)
    {
        this.nodes.update((nodes) => 
        {
            nodes.push(node);
            return nodes;
        });

        this.Save();
    }

    AddEdge(sourceId, targetId)
    {
        this.edges.update((edges) => 
        {
            edges.push({source : sourceId, target: targetId, id : (new Date).getTime().toString()});
            return edges;
        });

        this.Save();
    }

    UpdateNode (id, update)
    {
        this.nodes.update((nodes) => 
        {
            const node = nodes.find((node) => node.id === id);
            node.data = { ...node.data, ...update };
            return nodes;
        });

        this.Save();
    }

    Dump ()
    {
        return {
            nodes : get(this.nodes),
            edges : get(this.edges)
        };
    }

    Save()
    {
        const graph = this.Dump ();
        localStorage.graph = JSON.stringify(graph);
    }

    GetPrompt (targetId)
    {
        // GET EDGE
    
        const edgeList = get(this.edges);
        const sourceEdge = edgeList.find((edge) => edge.target === targetId);
    
        if (!sourceEdge)
            return "";
    
        // GET NODE
    
        const nodeList = get(this.nodes);
        const sourceNode = nodeList.find((node) => node.id === sourceEdge.source);
    
        // GET TEXT
    
        let result;
    
        switch (sourceNode.type)
        {
            case "textInput": result = {role:"user", text:`* ${sourceNode.data.value} *`}; break;
            case "generate": result = {role:"model", text:sourceNode.data.markdown}; break;
        }
    
        return [ ...this.GetPrompt(sourceNode.id), result];
    }
}