<script>

  import { Play, Clock } from 'lucide-svelte';
  import { Handle, Position, NodeResizer, useHandleConnections } from '@xyflow/svelte';

  import GraphState from '$lib/components/graph/common/Graph.svelte.js';
  import SettingsState from '$lib/components/settings/Settings.svelte.js';

  let {id, data} = $props();
  let inProgress = $state(false);

  const graph = GraphState.GetContext();
  const settings = SettingsState.GetContext();
  const connections = useHandleConnections({ nodeId: id, type: 'target' });
  const isConnectable = $derived ($connections.length === 0);

  async function onclickGenerate()
  {
    inProgress = true;

    const prompt = graph.GetPrompt(id);
    const contents = [];

    for (var i = 0; i < prompt.length; i++)
        contents.push({ role : prompt[i].role, parts : [{text : prompt[i].text}]});

    const { markdown, html } = await callApi(contents);
    graph.UpdateNode(id, { markdown, html });

    inProgress = false;
  }

  async function callApi(contents)
  {
    const url = "https://generativelanguage.googleapis.com/v1beta/models/gemini-2.0-flash-exp:generateContent?key=" + settings.geminiKey;

    try 
    {
      const httpResp = await fetch(url, 
      {
          method: "POST",
          headers: 
          {
              "Content-Type": "application/json"
          },
          body: JSON.stringify(
          {
            contents : contents,
            // generationConfig : {responseMimeType : "application/json"},
            safetySettings : [{ category : "HARM_CATEGORY_SEXUALLY_EXPLICIT", threshold : "BLOCK_NONE" }]
          })
      });

      if (!httpResp.ok)
          throw new Error(`API call failed with status ${httpResp.status}: ${httpResp.statusText}`);

      const geminiResp = await httpResp.json();
      const markdown = geminiResp.candidates[0].content.parts[0].text;
      
      // @ts-ignore
      const html = marked.parse(markdown);
      
      return { markdown, html };
    } 
    catch (error) 
    {
        console.error("Error calling Gemini AI API:", error);
        throw error;
    }
  }

</script>

<NodeResizer 
  minWidth={100} 
  minHeight={30} 
  onResizeEnd={() => graph.Save()} />

<Handle class="node-input" type="target" position={Position.Left} {isConnectable}/>
<Handle class="node-output" type="source" position={Position.Right} />

<node-content>
  <node-header>
    Generate 

    {#if !inProgress}
      <button class="button" title="Call LLM" aria-label="Call LLM" onclick={onclickGenerate}>
        <Play size={16}/>  
      </button>
    {:else}
      <button class="button" title="In progress..." aria-label="In progress..." disabled>
        <Clock size={16}/> 
      </button>
    {/if}

  </node-header>
  <node-body class="nodrag nozoom nomenu node-text">
    
      {@html data.html}

  </node-body>
</node-content>