import { setContext, getContext } from 'svelte';

const CONTEXT_KEY = Symbol("context-menu");

export default class ContextMenuState
{
    IsVisible = $state(false);
    Node = $state(null);
    Edge = $state(null);

    Top = $state(undefined);
    Left = $state(undefined);
    Right = $state(undefined);
    Bottom = $state(undefined);

    CanvasWidth = 0;
    CanvasHeight = 0;

    static SetContext() 
    {
        return setContext(CONTEXT_KEY, new ContextMenuState());
    }

    static GetContext()
    {
        return getContext(CONTEXT_KEY);
    }

    ShowPane (event) { this.#Show(event); }

    ShowNode (event, node) { this.#Show(event, node); }

    ShowEdge (event, edge) { this.#Show(event, null, edge); }

    ShowConnect(event, connection) { this.#Show(event, null, null, connection); }

    #Show (event, node, edge, connection) 
    {
        const noContextMenu = event.target.closest(".nomenu");

        if (noContextMenu || node || edge)
        {
            this.Hide();
            return;
        }

        this.Event = event;
        this.Node = node;
        this.Edge = edge;
        this.Connection = connection;
        event.preventDefault(); 

        this.Top = event.clientY < this.CanvasHeight - 200 ? event.clientY + "px" : undefined,
        this.Left = event.clientX < this.CanvasWidth - 200 ? event.clientX + "px" : undefined,
        this.Right = event.clientX >= this.CanvasWidth - 200 ? (this.CanvasWidth - event.clientX) + "px" : undefined,
        this.Bottom = event.clientY >= this.CanvasHeight - 200 ? (this.CanvasHeight - event.clientY) + "px" : undefined
        
        this.IsVisible = true;
    }

    Hide ()
    {
        if (this.IsVisible)
        {
            this.Event = null;
            this.Node = null;
            this.Edge = null;
            this.IsVisible = false;
        }
    }
}