import { setContext, getContext } from 'svelte';

const CONTEXT_KEY = Symbol("settings");

export default class SettingsState
{
    IsVisible = $state(false);
    geminiKey = $state("");

    static SetContext() 
    {
        return setContext(CONTEXT_KEY, new SettingsState());
    }

    static GetContext()
    {
        return getContext(CONTEXT_KEY);
    }

    constructor()
    {
        if (localStorage.settings)
        {
            const { geminiKey } = JSON.parse(localStorage.settings);
            this.geminiKey = geminiKey;
        }
    }

    Save()
    {
        localStorage.settings = JSON.stringify({ geminiKey : this.geminiKey });
        console.log("localStorage.settings", localStorage.settings);
    }

    Show () 
    {
        this.IsVisible = true;
    }

    Hide ()
    {
        this.IsVisible = false;
    }
}