<script lang="ts">
    import { useSvelteFlow } from '@xyflow/svelte';
    import ContextMenuState from '$lib/components/graph/menu/ContextMenu.svelte.js';
    import GraphState from '$lib/components/graph/common/Graph.svelte.js';

    const { screenToFlowPosition } = useSvelteFlow();     
    const graph = GraphState.GetContext(); 
    const contextMenu = ContextMenuState.GetContext();

    function addNode(newNode)
    {
      newNode.id = (new Date).getTime().toString();
      newNode.origin = [0.0, 0.0];

      newNode.position = screenToFlowPosition(
      {
        x : contextMenu.Event.clientX,
        y : contextMenu.Event.clientY
      });

      graph.AddNode(newNode);

      if (contextMenu.Connection)
      {
        if (contextMenu.Connection.fromHandle.type == "source")
        {
          graph.AddEdge(
            contextMenu.Connection.fromNode.id, 
            newNode.id);
        }
        else
        {
          graph.AddEdge(
            newNode.id, 
            contextMenu.Connection.fromNode.id);
        }
      }

      contextMenu.Hide();
    }

    function addTextInput()
    {
      addNode({type:"textInput", width: 262, height: 124, data : { value : "" }});
    }

    function addGenerate()
    {
      addNode({type:"generate", width: 430, height: 250, data : { markdown : "", html : "" }});
    }
   
    function nodeDuplicate() 
    {
      contextMenu.Hide();

    //   const node = $nodes.find((node) => node.id === id);

    //   if (node) 
    //   {
    //     $nodes.push(
    //     {
    //       ...node,
    //       // You should use a better id than this in production
    //       id: `${id}-copy${Math.random()}`,
    //       position: 
    //       {
    //         x: node.position.x,
    //         y: node.position.y + 50
    //       }
    //     });
    //   }
    //   $nodes = $nodes;
    }
   
    function nodeDelete() 
    {
      contextMenu.Hide();

    //   $nodes = $nodes.filter((node) => node.id !== id);
    //   $edges = $edges.filter((edge) => edge.source !== id && edge.target !== id);
    }

    function edgeDelete()
    {
      contextMenu.Hide();
    }

  </script>

{#if contextMenu.IsVisible}

  <div
    style:top={contextMenu.Top}
    style:left={contextMenu.Left}
    style:right={contextMenu.Right}
    style:bottom={contextMenu.Bottom}
    class="context-menu">
   
    {#if contextMenu.Node}

      <button class="context-menu-item" on:click={nodeDuplicate}>Duplicate node</button>
      <button class="context-menu-item" on:click={nodeDelete}>Delete node</button>

    {:else if contextMenu.Edge}

      <button class="context-menu-item" on:click={edgeDelete}>Delete edge</button>

    {:else}

      <div class="context-menu-item" style="padding: 0.5em;background: #ddd; color: #666;">Add node:</div>
      <button class="context-menu-item" on:click={addTextInput}>TextInput</button>
      <button class="context-menu-item" on:click={addGenerate}>Generate</button>

    {/if}

  </div>

{/if}
   
<style>

  .context-menu 
  {
    background: white;
    border-style: solid;
    box-shadow: 0.5em 0.5em 0 rgba(255, 255, 255, 0.2);
    position: absolute;
    z-index: 10;
    min-width: 9em;
  }
  
  .context-menu-item 
  {
    color: black;
    border: none;
    display: block;
    padding: 0.5em;
    text-align: left;
    width: 100%;
    text-shadow: 0.05em 0.05em 0.15em rgba(255, 255, 255,0.6);
  }
  
  .context-menu button:hover 
  {
    background: rgb(255, 225, 190);
  }

</style>