<script>
    import { onMount } from 'svelte';
    import { Settings, Download, Upload } from 'lucide-svelte';
    
    import GraphState from '$lib/components/graph/common/Graph.svelte.js';
    import SettingsState from '$lib/components/settings/Settings.svelte.js';

    const graph = GraphState.GetContext();
    const settings = SettingsState.GetContext();
    
    let fileInput;
    let settingsDialog;

    onMount(() => 
    {
        if (!settings.geminiKey)
            settingsDialog?.showModal();
	});   

    function fileChange(event)
    {
        const file = event.target.files[0];

        if (!file) 
            throw "Invalid file";

        if (file.type != "application/json")
            throw "Invalid file";

        const reader = new FileReader();

        reader.onload = (e) => 
        { 
            const graphJson = JSON.parse(e.target.result.toString());

            if (!Array.isArray(graphJson.nodes)) 
                throw "Invalid file";

            if (!Array.isArray(graphJson.edges)) 
                throw "Invalid file";

            graph.LoadFromFile(graphJson);
        };

        reader.readAsText(file);
    }
    
    function onclickExport() 
    {
        const jsonData = graph.Dump ();
        const jsonString = JSON.stringify(jsonData, null, 2);
        const blob = new Blob([jsonString], { type: "application/json" });
        const url = URL.createObjectURL(blob);

        const a = document.createElement('a');
        a.href = url;
        a.download = "llm-graph.json"; 
        a.click();

        URL.revokeObjectURL(url);
    }

</script>

<input 
    type="file" 
    id="fileInput" 
    style="display: none;" 
    accept=".json"
    bind:this={fileInput}
    onchange={fileChange} />
  
<div class="svelte-flow__panel svelte-flow__controls left top">
    <button onclick={() => fileInput.click()} class="menu-button" type="button" title="Import" aria-label="Import">
        <Upload size={16} />
    </button>
    <button onclick={onclickExport} class="menu-button" type="button" title="Export" aria-label="Export">
        <Download size={16} />
    </button>
    <button onclick={() => settingsDialog.showModal()} class="menu-button" type="button" title="Settings" aria-label="Settings">
        <Settings size={16} />
    </button>
</div>

<dialog class="modal" id="settingsDialog" bind:this={settingsDialog} style="background-color: rgba(0, 0, 0, 0.85);">
    <div class="modal-box">
        
        <h3 class="text-lg font-bold">Settings</h3>
        
        <label class="input input-bordered flex items-center gap-2">
            Gemini API Key:
            <input type="text" class="grow" bind:value={settings.geminiKey} onchange={() => settings.Save()} />
        </label>
        
        <div class="modal-action">
            <form method="dialog">
                <button class="btn">Close</button>
            </form>
        </div>
    </div>
</dialog>
