<div 
  style:height="100%" 
  bind:clientWidth={contextMenu.CanvasWidth} 
  bind:clientHeight={contextMenu.CanvasHeight}>

  <SvelteFlow
    nodes = {graph.nodes}
    edges = {graph.edges}
    {nodeTypes}
    fitView
    fitViewOptions = {{maxZoom:1,minZoom:1}}
    proOptions = {{hideAttribution:true}}
    colorMode = "dark"
    snapGrid = {[20,20]}
